import { Component, OnInit, HostListener } from '@angular/core';
import { MainService } from '../main.service';
import { Router } from '@angular/router';
import * as $ from 'jquery';

@Component({
  selector: 'app-slideshow',
  templateUrl: './slide-show.component.html',
  styleUrls: ['./slide-show.component.scss']
})
export class SlideShowComponent implements OnInit {
  text: string = 'Default';
  time: number = 100;
  interval: any;
  constructor(private mainService: MainService, private router: Router) { }

  ngOnInit() {
    this.text = this.mainService.getText();
    // this.pageScroll()
  }

  isBottom() {
    if($(window).scrollTop() + $(window).height() == $(document).height()) {
      return true
    }
    return false
  }

  pageScroll() {
    clearInterval(this.interval );
    let speed = 1
    if( (this.time / 5) < 0){
      speed = Math.abs(this.time / 5);
    }
    this.interval = setInterval(()=>{
      window.scrollBy(0,speed);
      if (this.isBottom()) clearInterval(this.interval);
    }, this.time)
  }

  @HostListener('document:keypress', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) { 
    event.preventDefault();
    let key = event.keyCode;
    if(key == 44) {
      this.time += 5;
      this.pageScroll()
    }else if(key == 46){
      this.time -= 5;
      this.pageScroll()
    }else if(key == 32){
      if(this.interval){
        clearInterval(this.interval);
        this.interval = null;
      }else{
        if(this.isBottom()) return; 
        this.pageScroll();
      } 
    }else if(key == 116){
      window.scrollTo(0,0);
    }else if(key == 49){
      let size = parseInt($('.slide').css('fontSize').replace('px', ''));
      $('.slide').css('fontSize', size - 10);
    }else if(key == 50){
      let size = parseInt($('.slide').css('fontSize').replace('px', ''));
      $('.slide').css('fontSize', size + 10);
    }else if(key == 51){
      let size = parseInt($('.slide').css('lineHeight').replace('px', ''));
      if(!size) size = 100;
      $('.slide').css('lineHeight', (size - 1) + 'px');
    }else if(key == 52){
      let size = parseInt($('.slide').css('lineHeight').replace('px', ''));
      if(!size) size = 100;
      $('.slide').css('lineHeight', (size + 1) + 'px');
    }
  }
}
